export function AgeTag({ callbackFromParent }) {
  const classes = useStyles();
  let age = 0;
  const inputChange = e => {
    age = Math.round(e.target.value); //.replace(/[^0-9]/g, "")
  };
  return (
    <div style={{ maxHeight: "50px" }}>
      <Typography className={classes.title} color="primary" gutterBottom>
         .
      </Typography>
      <Typography
        variant="caption"
        color="secondary"
        style={{ marginLeft: "10px" }}
      >
        * 20   .
      </Typography>
      <Grid
        container
        direction="row"
        justify="center"
        alignItems="center"
        style={{ marginTop: "100px" }}
      >
        <FormControl>
          {/* <Input
            type="number"
            onChange={inputChange}
            style={{ marginRight: "10px" }}
            inputMode="numeric"
          /> */}
          <input
            className="ageInput"
            type="number"
            onChange={inputChange}
            inputMode="numeric"
            pattern="[0-9]*"
            placeholder="20"
          />
        </FormControl>
        <Button
          variant="contained"
          color="primary"
          position="end"
          onClick={() => callbackFromParent(age)}
          style={{ boxShadow: "none" }}
        >
          
        </Button>
      </Grid>
    </div>
  );
}
