import React, { Component } from "react";
import { withCookies, Cookies } from "react-cookie";
import { instanceOf } from "prop-types";
import { CssBaseline, Container, CircularProgress } from "@material-ui/core/";
import { postGet } from "../Axios/messageModel";
import MessageList from "../Components/Message/MessageList";
import BackAppBar from "../Components/AppBar/BackAppBar";

class Message extends Component {
  static propTypes = {
    cookies: instanceOf(Cookies).isRequired
  };
  constructor(props) {
    super(props);
    const { cookies } = this.props;
    this.state = {
      uid: cookies.get("_id"),
      messages: [],
      page: 0,
      isLoading: true,
      isScrolling: false
    };
  }
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
    this.getMessage(this.state.page);
  }
  componentWillUnmount() {
    //  ,   
    window.removeEventListener("scroll", this.handleScroll);
  }
  handleScroll = () => {
    const { innerHeight } = window;
    const { scrollHeight } = document.body;
    // IE document.documentElement  .
    const scrollTop =
      (document.documentElement && document.documentElement.scrollTop) ||
      document.body.scrollTop;
    //  ,    100   .
    if (scrollHeight - innerHeight - scrollTop < 100) {
      if (!this.state.isScrolling) {
        this.setState({
          isScrolling: true,
          page: this.state.page + 10
        });
        this.getMessage(this.state.page);
      }
    }
  };
  getMessage = async page => {
    const data = await postGet(this.state.uid, page);
    if (data.complete) {
      this.setState({
        messages: this.state.messages.concat(data.message),
        isScrolling: false,
        isLoading: false
      });
    }
  };
  callbackRemove = () => {
    this.setState({
      messages: []
    });
    this.getMessage(0);
  };
  render() {
    const { isLoading, messages } = this.state;
    return (
      <div>
        <CssBaseline />
        <BackAppBar name="" />
        {isLoading ? (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center"
            }}
          >
            <CircularProgress color="primary" />
          </div>
        ) : (
          messages.map((item, index) => (
            <MessageList
              key={index}
              message={item}
              callbackRemove={this.callbackRemove}
            />
          ))
        )}
      </div>
    );
  }
}

export default withCookies(Message);
